.testimonial {
    margin: 64px 32px 16px 32px;
}

.testimonial-image {
    width: 80px;
    height: 80px;
    margin: 0 auto 0 auto;
    border-radius: 50px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-x: center;
    background-position-y: center;
    filter: drop-shadow(0 0 12px darkgrey);
    pointer-events: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.testimonial-text {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-around;
    width: 100%;
    max-width: 450px;
    height: 60%;
    padding-bottom: 28px;
}

.testimonial-text h1 p{
    margin: 0 0 0 0;
    padding: 0 0 0 0;
}

.testimonial-stars {
    display: flex;
    flex-direction: row;
    text-align: center;
    justify-content: space-around;
    width: 90%;
    max-width: 300px;
    margin: 16px auto 8px auto;
    filter: drop-shadow(0 0 12px darkgrey);
    pointer-events: none;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
