.global-navbar {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  filter: drop-shadow(0 3px 8px black);
}

.global-navbar-divider {
  min-width: 300px;
  width: 60%;
  font-size: 1px;
  background-color: rgba(0,0,0,.5);
  filter: drop-shadow(1px 1px 10px black);
}

.logo {
  padding: 84px 0 32px 0;
  min-width: 300px;
  width: 60%;
  filter: drop-shadow(1px 1px 1px black);
}

.menu-buttons {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  min-width: 100vw;
  justify-content: space-between;
  background-color: rgb(45, 45, 45);
}

.menu-buttons>button {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  background-color: unset;
  border: unset;
  inset: unset;
  color: white;
  width: 100%;
  height: 48px;
  font-size: calc(8px + 2vmin);
  border-right: 3px solid rgb(170, 170, 170, .1);
  cursor: pointer;
  pointer-events: all;
}

.menu-buttons>button:focus {
  background-color: rgba(192, 225, 247, 0.75);
  border-top: 3px solid rgb(36, 154, 233);
  border-bottom: 3px solid rgb(36, 154, 233);
  outline-style: none;
}

.menu-buttons>button:hover {
  border-top: 3px solid rgb(36, 154, 233);
  border-bottom: 3px solid rgb(36, 154, 233);
}

@media (max-width: 600px) {
  .global-navbar-divider {
    width: 90%;
  }
  .logo {
    width: 90%;
  }
  .menu-buttons {
    width: 90%;
  }
}

@media (min-width: 1000px) {
  .global-navbar-divider {
    width: 60%;
  }
  .logo {
    width: 30%;
  }
  .menu-buttons {
    width: 50%;
  }
}
