.image-section {
    display: flex;
    height: 420px;
    min-height: 320px;
    max-height: 720px;
    align-content: center;
    justify-content: center;
    overflow: hidden;
}

.home-image {
    width: auto;
    height: auto;
    filter: drop-shadow(1px 1px 8px black);
}

.image-section-text {
    margin: 32px 5% 0 5%;
    width: 50%;
}

.image-section p {
    text-align: center;
    min-height: 300px;
    min-width: 300px;
    max-height: 720px;
    font-size: calc(9px + 1vmin);
}

.image-section h3 {
    text-align: center;
    color: rgb(48, 48, 48);
    font-size: calc(10px + 2vmin);
}

.light-section {
    flex-direction: row-reverse;
    background-color: #eeeeee;
}

@media (max-width: 600px) {
    .image-section {
        padding: 0 1% 0 1%;
    }
}

@media (max-width: 999px) {
    .image-section {
        flex-direction: column;
        height: 50vmin;
        margin-bottom: 32px;
    }
    .image-section-text {
        height: 50%;
        width: 90%;
    }
    .home-image {
        object-fit: cover;
        width: 110%;
        height: 50%;
        margin-left: -5%;
        overflow: hidden;
    }
}

@media (min-width: 1000px) {
    .image-section {
        padding: 0 10% 0 10%;
    }
}
